<template>
  <div class="part-seven">
    <h3 class="text-center">求职意向</h3>
    <p class="text-small"><span class="font-bold">期望职位：</span>Web前端开发实习生</p>
    <p class="text-small"><span class="font-bold">工作技能：</span>Vue</p>
    <p class="text-small"><span class="font-bold">目标城市：</span>广州、深圳、杭州、武汉、上海</p>
    <p class="text-small"><span class="font-bold">入职时间：</span>随时入职</p>
  </div>
</template>

<script>
export default {
  name: "partSeven",
  data() {
    return {
      
    };
  }
};
</script>

<style scoped>
@-webkit-keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}
@keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}
.part-seven {
  width: 100%;
  height: 450px;
  border: 40px solid transparent;
  border-image: url("~@/./assets/img/border_image.png") 30 30 stretch;
  background: #18202d;
}

.text-center {
    text-align: center;
    margin-bottom: 30px;
    color: hsla(0, 0%, 100%, .65);
    font-weight: 800;
    background-image: -webkit-linear-gradient(left, #00bfff, #207ffc 25%, #00eeff 50%, #0066ff 75%, #00ffdd);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-background-size: 200% 100%;
    background-size: 200% 100%;
    -webkit-animation: masked-animation 2s infinite linear;
    animation: masked-animation 2s infinite linear;

}
.text-center:hover {
     -webkit-animation: masked-animation 0.5s infinite linear;
     animation: masked-animation 0.5s infinite linear;
     cursor: pointer;
}

.text-small {
  font-size: .9em;
  line-height: 30px;
}
.font-bold {
  text-align: center;
  color: deepskyblue;
}
</style>


